.nav {
    grid-column: 2 / 3;
    padding: 20px;
}

.item {
    height: 28px;
    line-height: 27px;
    padding-left: 5px;
}

.item a {
    color: #2a5885;
    text-decoration: none;
    display: flex;
}

.item a img {
    width: 17px;
    padding-right: 12px;
}

.item:hover {
    background-color: #dce1e6;
}

.item a.activeLink {
    color: #569eff;
}

.friends {
    margin-top: 20px;
    font-size: 30px;
}